@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Display:wght@300;400;500;600;700&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Noto Sans Display', sans-serif;
}

.App {
  background-color: #fff;
}

/** Search **/
.search {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #485bb1;
  padding: 40px 10px;
}

.search h2 {
  position: relative;
  margin-top: 10px;
  color: #fff;
  font-weight: 400;
}

.search-title {
  text-align: center;
  margin-bottom: 10px;
  word-spacing: 0.15em;
  color: #fff;
  font-weight: 500;
  font-size: 35px;
}

.search-form {
  justify-content: center;
  display: flex;
}

.search-input {
  outline: none;
  width: 300px;
  height: 35px;
  border: none;
  padding: 0;
}

input[type="text"] {
  padding: 10px;
}

.search-input:placeholder-shown {
  padding-left: 10px;
}

.search-btn {
  position: relative;
  color: #fff;
  background: #44aaee;
  cursor: pointer;
  display: block;
  font-size: 18px;
  font-weight: 400;
  letter-spacing: 0.04em;
  width: 100px;
  height: 35px;
  border: none;
  transition: background .2s;
}

.search-btn:hover {
  background: #5db7f3;
  transition: all .3s;
}


.search-filter {
  margin-top: 5px;
  display: flex;
  justify-content: space-between;
  width: 400px;
}

.search-filter h3 {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 28px;
  font-size: 16px;
  font-weight: 400;
  color: #fff;
}

.search-select {
  width: 120px;
  height: 28px;
  outline: none;
}


/** Link books **/

.books_wrapp {
  
  justify-content: space-evenly;
  margin: 20px auto;
  max-width: 1000px;
  display: flex;
  flex-wrap: wrap;
}

.link-book {
  display: block;
  text-decoration: none;
  padding: 50px 10px;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 20px 10px;
  width: 300px;
  height: 400px;
  overflow: hidden;
  background-color: #99a3ce75;
  transform: scale(1);
  transition: transform .3s;
}

.link-book:hover {
  transform: scale(1.04);
  transition: transform .3s;
}

.link-book img {
  width: auto;
  height: 170px;
}

.book_info {
  margin-top: 15px;
  text-align: center;
}

.book_title {
  color: #212121;
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 0.04em;
}

.book_categories {
  color: #414141;
  font-size: 14px;
  font-weight: 400;
}

.book_authors {
  color: #313131;
  font-size: 18px;
  font-weight: 500;
}


/** loading **/

.loading_wrapp {
  top: 0;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #ffffffdf;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: -1;
  opacity: 0;
}

.loading_wrapp-active {
  z-index: 1;
  opacity: 1;
  transition: all .3s;
}

.loading_wrapp h2 {
  font-size: 40px;
  letter-spacing: 0.05em;
  color: #485bb1;
}


/** loadmore**/

.loadmore-wrapp {
  text-align: center;
  padding-bottom: 100px;
}

.loadmore-btn {
  cursor: pointer;
  border: none;
  width: 200px;
  height: 50px;
  background: #44aaee;
  color: #fff;
  font-size: 20px;
  transition: background .3s;
}

.loadmore-btn:hover {
  transition: background .3s;
  background: #5db7f3;
}

/** itemBook **/
.section-itemBook {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.itemBook {
  box-shadow: 6px 6px #99a3ce75, 12px 12px #99a3ce75;
  position: relative;
  overflow: hidden;
  padding: 20px;
  background-color: #99a3ce75; 
  width: 600px;
  min-height: 400px;
  display: flex;
  flex-wrap: wrap;
}

.itemBook-img {
  margin-right: 20px;
  width: 160px;
  height: 210px;
}

.itemBook-info {
  margin-bottom: 10px;
  width: 310px;
  color: #212121;
}

.itemBook-title {
  font-size: 35px;
  letter-spacing: .02em;
}

.itemBook-categories {
  font-size: 18px;
  font-weight: 500;
}

.itemBook-link {
  position: absolute;
  top: 20px;
  right: 20px;
}



/** scroll **/
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
::-webkit-scrollbar-thumb {
  background: #44aaee;
}
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/** itemBook **/




@media (max-width: 420px) {
  .search-input {
    width: 200px;
  }
  .search-btn {
    width: 80px;
  }
  .search-filter h3 {
    font-size: 14px;
  }
  .search-filter {
    width: 280px;
  }
  .search-select {
    width: 60px;
  }
}